.Dashboard {
    display: flex;
  }
  .Main {
    display: flex;
    flex-basis: 60%;
    flex-direction: column;
  }
  .Map {
    flex-grow: 1;
    height: 100vh;
    top: 0;
    position: sticky;
  }
  .Segments {
    display: flex;
    flex-wrap: wrap;
    align-content: flex-start;
  }
  
  .Search {
    padding: 10px;
    margin-right: 5px;
    border-radius: 10px;
    font-family: 'Lato';
  }
  
  .Search input{
    width: 100%;
    height: 40px;
    font-size: 24px;
    color: #999; 
    padding-left: 5px;
  }