.App {
    display: flex;
  }

.form-signin {
    width: 100%;
    max-width: 330px;
    padding: 15px;
    margin: auto;
    text-align: center !important;
    margin-top: 10%;
    background-color: rgb(230,230,250);
    border-radius: 5%;
    box-shadow: 3px 3px rgba(0,0,0,0.5);
}

.form-signin .form-control {
  position: relative;
  box-sizing: border-box;
  height: auto;
  font-size: 16px;
}
.Segment {
    flex-basis: calc(50% - 20px);
    margin: 10px;
    cursor: pointer;
  }
  .Segment:hover{
      opacity: 0.9;
  }
  .Segment-picture {
      height: 200px;
      background-size: cover;
      background-position: center;
  }
  .Segment-title {
      margin-top: 10px;
      font-weight: 900;
  }
  .Segment-price {
    margin-top: 10px;
    color: #808080; 
  }
.Marker {
	width: 48px;
	background-color: #ffffff;
	border: 1px solid #ffffff;
	border-radius: 8px;
	text-align: center;
	padding: 4px 0;
	font-size: 16px;
	font-family: 'Lato';
	font-weight: bold;
}
.Marker.Selected {
	background-color: #aa8cc5;
	border-color: black;
}
.Dashboard {
    display: flex;
  }
  .Main {
    display: flex;
    flex-basis: 60%;
    flex-direction: column;
  }
  .Map {
    flex-grow: 1;
    height: 100vh;
    top: 0;
    position: -webkit-sticky;
    position: sticky;
  }
  .Segments {
    display: flex;
    flex-wrap: wrap;
    align-content: flex-start;
  }
  
  .Search {
    padding: 10px;
    margin-right: 5px;
    border-radius: 10px;
    font-family: 'Lato';
  }
  
  .Search input{
    width: 100%;
    height: 40px;
    font-size: 24px;
    color: #999; 
    padding-left: 5px;
  }
